import styled from "styled-components";

const homePage = styled.div`
.Home {
    min-height: 100vh;
    max-width: 100vw;
    font-family: 'Urbanist', sans-serif;
        .Header {
        display: flex;
        justify-content: space-between;
        padding: 30px 0 0 0;
        width: 90vw;
        margin: 0 auto;
        .Logo {
            img {
                width: 80px;
            }
        }
        .Buttons {
            display: flex;
            gap: 10px;
            align-items: center;
            button {
                border: none;
                background-color: transparent;
                font-size: 14px;
                cursor: pointer;
                transition: .3s;

                :hover {
                    color: #7510f7;
                    transform: .3s;
                }
            }
            a {
                &:nth-child(2) {
                    text-decoration: none;
                    padding: 10px 15px;
                    border: 1px solid #7510f7;
                    background-color: transparent;
                    font-size: 14px;
                    font-weight: 600;
                    border-radius: 50px;
                    color: #7510f7;
                    cursor: pointer;
                    transition: .3s;

                    :hover {
                        background-color: #7510f7;
                        color: #FFF;
                        transform: .3s;
                    }
                }
            }
        }
    }
    .MainContent {
        background-position: center;
        background-size: cover;
        position: relative;
        width: 100%;
        height: 90vh;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 50px;
        .Avatar {
            img {
                width: 250px;
            }
        }
        small {
            position: absolute;
            bottom: 0;
            display: flex;
            justify-content: center;
            width: 100%;
            margin: 20px 0;
            font-size: 20px;
            font-weight: 500;
            color: #7510f7;
            display: flex;
            flex-direction: column;
            align-items: center;
            line-height: 20px;

            svg {
                margin-top: 10px;
                font-size: 16px;
                animation: float 2s infinite linear;
            }
            @keyframes float{
                0%,100%{
                    transform:translatey(0)
                }
                50%{
                    transform:translatey(-6px)
                }
            }
        }
        h1 {
            margin: -50px auto 0 auto;
            color: #272822;
            font-size: 28px;
            font-weight: 500;
            text-align: center;
            width: 90vw;

            svg {
                color: #7510f7;
            }
        }
    }
    .Animation {
        height: 300px;
        position: relative;
        display: flex;
        justify-content: center;
        .LootieAnimation {
            position: absolute;
            bottom: -18px;
            svg {
                filter: drop-shadow(5px 1px 1px rgba(0,0,0, .5));
            }
        }
    }
    .Apresentacao {
        height: 600px;
        background-color: #6e07f3;
        color: #FFF;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            text-align: center;
            margin-top: 100px;
            font-size: 24px;
        }
        p {
            width: 90vw;
            margin: 0 auto;
            font-size: 15px;
            text-align: justify;
        }
    }
    .skills {
        height: 100vh;
        .skillsContent {
            width: 90vw;
            height: 700px;
            background-color: #FFF;
            margin: -200px auto;
            border-radius: 10px;
            border: 1px solid rgba(110, 7, 243, .2);
            box-shadow: 2px 2px 4px rgba(0,0,0, .5);
        }
    }
}
`

export default homePage;