import styled from "styled-components";

const homePage = styled.div`
.Home {
    min-height: 100vh;
    max-width: 100vw;
    font-family: 'Urbanist', sans-serif;
        .Header {
        display: flex;
        justify-content: space-between;
        padding: 30px 0 0 0;
        width: 80vw;
        margin: 0 auto;
        .Logo {
            img {
                width: 80px;
            }
        }
        .Buttons {
            display: flex;
            align-items: center;
            gap: 15px;
            text-decoration: none;
            button {
                border: none;
                background-color: transparent;
                font-size: 16px;
                cursor: pointer;
                transition: .3s;

                :hover {
                    color: #7510f7;
                    transition: .3s;
                }
            }
            a {
                &:nth-child(2) {
                    text-decoration: none;
                    font-weight: 600;
                    padding: 10px 20px;
                    border: 1px solid #7510f7;
                    background-color: transparent;
                    font-size: 14px;
                    border-radius: 50px;
                    color: #7510f7;
                    cursor: pointer;
                    transition: .3s;

                    :hover {
                        background-color: #7510f7;
                        color: #FFF;
                        transform: .3s;
                    }
                }
            }
        }
    }
    .MainContent {
        background-position: center;
        background-size: cover;
        position: relative;
        width: 100%;
        height: 600px;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 50px;
        .Avatar {
            img {
                width: 250px;
            }
        }
        small {
            &:nth-child(3){
                position: absolute;
                bottom: 0;
                display: flex;
                justify-content: center;
                width: 100%;
                margin: 20px 0;
                font-size: 20px;
                font-weight: 500;
                color: #7510f7;
                display: flex;
                flex-direction: column;
                align-items: center;
                line-height: 20px;
            }
            &:nth-child(2) {
                font-weight: 400;
                font-size: 20px;
            }
                
            svg {
                margin-top: 10px;
                font-size: 16px;
                animation: float 2s infinite linear;
            }
            @keyframes float{
                0%,100%{
                    transform:translatey(0)
                }
                50%{
                    transform:translatey(-6px)
                }
            }
        }
        h1 {
            margin: -50px auto 0 auto;
            color: #272822;
            font-size: 38px;
            font-weight: 600;
            text-align: center;

            svg {
                color: #7510f7;
            }
        }
    }
    .Animation {
        height: 500px;
        position: relative;
        display: flex;
        justify-content: center;
        .LootieAnimation {
            position: absolute;
            bottom: -32px;

            svg {
                filter: drop-shadow(5px 1px 1px rgba(0,0,0, .5));
            }
        }
    }
    .Apresentacao {
        height: 600px;
        background-color: #6e07f3;
        color: #FFF;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;

        h2 {
            margin-top: 100px;
            font-size: 32px;
        }
        p {
            width: 650px;
            margin: 0 auto;
            font-size: 18px;
        }
    }
    .skills {
        height: 100vh;
        .skillsContent {
            width: 80vw;
            height: 700px;
            background-color: #FFF;
            margin: -200px auto;
            border-radius: 15px;
            border: 1px solid rgba(110, 7, 243, .2);
        }
    }
}
`

export default homePage;