import React from "react";
import StyleDesktop from "./style-desktop";
import StyleMobile from "./style-mobile";
import CheckDevice from "../../hooks/verify-device";
import Logo from "../../assets/logo.png";
import Avatar from "../../assets/avatar.png";
import { Player } from "@lottiefiles/react-lottie-player";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronDown } from "@fortawesome/free-solid-svg-icons";
import {
  faInstagram,
  faWhatsapp,
  faGithub,
} from "@fortawesome/free-brands-svg-icons";

const Component = () => {
  const isMobile = CheckDevice();

  const scrollToArea = () => {
    var scrollDiv = document.getElementById("Apresentacao").offsetTop;
    window.scrollTo({ top: scrollDiv, behavior: "smooth" });
  };

  console.log(isMobile);

  if (!isMobile) {
    return (
      <StyleDesktop>
        <div className="Home">
          <div className="Header">
            <div className="Logo">
              <img src={Logo} alt="" />
            </div>
            <div className="Buttons">
              <button onClick={scrollToArea}>sobre mim</button>
              <a
                href="https://wa.me/5551994666017?text=Ol%C3%A1%2C%20vim%20atrav%C3%A9s%20do%20seu%20portf%C3%B3lio."
                target="_blank"
                rel="noreferrer"
              >
                Diga ol
              </a>
            </div>
          </div>
          <main className="MainContent">
            <h1>
              Ol, me chamo Daniel, sou desenvolvedor Web e Mobile.
              <br />
              <small>
                Crio telas, sites e interfaces de usurio dinmicas e inuitivas.
                D vida ao seu projeto agora mesmo!
              </small>
            </h1>
            <div className="Avatar">
              <img src={Avatar} alt="" />
            </div>
            <small className="seeMore">
              Saiba mais sobre o que fao
              <FontAwesomeIcon icon={faChevronDown} />
            </small>
          </main>
          <div className="Animation">
            <div className="LootieAnimation">
              <Player
                autoplay
                loop
                src="https://assets5.lottiefiles.com/packages/lf20_bdtph16r.json"
                style={{ height: "450px", width: "450px" }}
              />
            </div>
          </div>
          <div className="Apresentacao" id="Apresentacao">
            <h2> um prazer te-lo aqui, seja muito bem-vindo!</h2>
            <p>
              Agora, deixe-me apresentar. Sou criativo, curioso, auto motivado e
              fascinado por inovao. Estou sempre em busca de conhecimentos que
              possibilitem expandir minhas skills em tecnologias que trabalho
              atualmente e buscando aprender sempre algo novo que agregue de
              forma positiva a minha equipe. Adaptao, foco no resultado e
              resilincia so minhas principais soft skills.
            </p>
          </div>
          <div className="skills">
            <div className="skillsContent"></div>
          </div>
        </div>
      </StyleDesktop>
    );
  }

  return (
    <StyleMobile>
      <div className="Home">
        <div className="Header">
          <div className="Logo">
            <img src={Logo} alt="" />
          </div>
          <div className="Buttons">
            <button onClick={scrollToArea}>sobre mim</button>
            <a
              href="https://wa.me/5551994666017?text=Ol%C3%A1%2C%20vim%20atrav%C3%A9s%20do%20seu%20portf%C3%B3lio."
              target="_blank"
              rel="noreferrer"
            >
              Diga ol
            </a>
          </div>
        </div>
        <main className="MainContent">
        <h1>
            Ol, me chamo Daniel, desenvolvo telas, sites e <br /> interfaces de
            usurio dinmicas e intuitivas.
          </h1>
          <div className="Avatar">
            <img src={Avatar} alt="" />
          </div>
          <small className="seeMore">
            Saiba mais sobre o que fao
            <FontAwesomeIcon icon={faChevronDown} />
          </small>
        </main>
        <div className="Animation">
          <div className="LootieAnimation">
            <Player
              autoplay
              loop
              src="https://assets5.lottiefiles.com/packages/lf20_bdtph16r.json"
              style={{ height: "250px", width: "250px" }}
            />
          </div>
        </div>
        <div className="Apresentacao" id="Apresentacao">
          <h2> um prazer te-lo aqui,<br />seja muito bem-vindo!</h2>
          <p>
            Agora, deixe-me apresentar. Sou criativo, curioso, auto motivado e
            fascinado por inovao. Estou sempre em busca de conhecimentos que
            possibilitem expandir minhas skills em tecnologias que trabalho
            atualmente e buscando aprender sempre algo novo que agregue de forma
            positiva a minha equipe. Adaptao, foco no resultado e resilincia
            so minhas principais soft skills.
          </p>
        </div>
        <div className="skills">
          <div className="skillsContent"></div>
        </div>
      </div>
    </StyleMobile>
  );
};

export default Component;
